  color: #dedede;
  font-size: 1.1rem;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.bg-litered {
  background-color: #ff7f74;
}

.bg-litegreen {
  background-color: #018786;
}

.bg-lightGray {
